<template>
  <div class="bg-white p-3 rounded-md">
    <PageTitle>
      Admin | Markazlarga Xodim qo'shish
    </PageTitle>
    <AddWorkerToCenterAdminForm :clear-form-mode="clearFormFields" @submit="onSubmitHandler" />
  </div>
</template>

<script setup>
import {ref} from 'vue';
import axios from "../services/api/axios";
import useAlert from "../composables/useAlert";
import AddWorkerToCenterAdminForm from "../components/AddWorkerToCenterAdminForm";
import PageTitle from "../components/LayoutItems/PageTitle";
import useErrorAlert from "../composables/useErrorAlert";


const {alertIt} = useAlert()


const clearFormFields = ref(false)
const {alertThisError} = useErrorAlert()

const onSubmitHandler = async (formData) => {
  try {
    const {data} = await axios.post(`/auth/userCreateForApparat/`, formData)
    if(data?.message){
      await alertIt(data.message)
    }
    clearFormFields.value = true
  } catch (e) {
    alertThisError(e)
  }
}


</script>

<style scoped>
span {
  @apply text-red-400 h-5
}
</style>
